import styled, { keyframes } from "styled-components";
import oz from "../cat.png";

const StarSC = styled.div`
  top: -10px;
  z-index: -1;
  position: absolute;
  background-color: Cyan;
  left: ${props => props.x}px;
  animation: ${() => moveY} ${props => props.sp}s linear
    ${props => `${props.delay}ms`} infinite normal;
`;
const shoot = keyframes`
  0%    { top: 0; left: 0; transform: rotate(20deg); }
  100%  { top: 100%; left: 100%; transform: rotate(200deg);  }
`;
export const SmStarSC = styled(StarSC)`
  width: 1px;
  height: 1px;
`;

export const MdStarSC = styled(StarSC)`
  width: 2px;
  height: 2px;
`;

export const LgStarSC = styled(StarSC)`
  width: 3px;
  height: 3px;
`;

export const WorldContainer = styled.div`
  margin: 10vh auto;
  height: ${window.innerHeight > window.innerWidth ? "80vw" : "80vh"};
  width: ${window.innerHeight > window.innerWidth ? "80vw" : "80vh"};
  position: relative;
  animation: rotate infinite 60s;
  @media (max-width: 400px) {
    // margin-top: 20vh;
    height: ${window.innerHeight > window.innerWidth ? "90vw" : "90vh"};
    width: ${window.innerHeight > window.innerWidth ? "90vw" : "90vh"};
  }
`;

export const Oscar = styled.div`
  height: ${window.innerHeight > window.innerWidth ? "5vw" : "5vh"};
  width: ${window.innerHeight > window.innerWidth ? "5vw" : "5vh"};
  background-image: url(${oz});
  background-size: cover;
  position: absolute;
  top: -50px;
  z-index: -1;
  animation: ${shoot} infinite 7s linear;
  animation-delay: 30s;
  animation-iteration-count: 1;
`;

// Stars and Enemies will use this to go from end to end
// of the Container
const moveY = keyframes`
  0%    { transform: translateY(-100px);  }
  100%  { transform: translateY(${window.innerHeight}px);   }
`;

const rotate = keyframes`
  0%    { transform: translateY(-100px);  }
  100%  { transform: translateY(950px);   }
`;
